<script lang="ts">
import {defineComponent} from 'vue'
import KeyPanel from "@/components/editor/form/keyPanel.vue";
import InputString from "@/components/editor/form/inputString.vue";
import DashboardEditorObject from "@/components/dashboard/dashboardEditorObject.vue";


export default defineComponent({
  name: "dashboardEditor",
  components: {DashboardEditorObject, InputString, KeyPanel},
  props: ['object'],
  provide: {open: {}},
  data() {
    return {
      owner: {},
      moduleSchema: [],
      saving: false
    }
  },
  methods: {
    async init() {
      this.moduleSchema = this.object._module.Object.schema
      this.owner = await this.object._owner
    },
    async save() {
      await this.object._save()
    }
  },
  mounted() {
    this.init()
  }
})
</script>

<template>
  <div class="dashboardEditor">
    <div class="headerBox">
      <div class="titleBox">
        <h3>{{ object._title }}</h3>
        <n-button-group>
          <n-button :loading="saving" :type="object._changed?'default':'tertiary'" @click="save()">
            <!--            <n-icon size="21">-->
            <!--              <file-upload-outlined/>-->
            <!--            </n-icon>-->
            保存
          </n-button>
        </n-button-group>
      </div>
      <p style="font-size: small;opacity: 0.6;cursor: pointer" v-clipboard="object._place.valueOf()" title="复制地址">
        {{ object._place }}
      </p>
      <div class="infoBox">
        <n-space>
          <n-tag size="small">所有者:{{ owner._title }}</n-tag>
          <n-tag size="small">更新@{{ $moment(object._updatedAt).calendar() }}</n-tag>
        </n-space>
      </div>
    </div>
    <input-string v-model="object._title" :_key="moduleSchema.title||{title:'标题'}"/>
    <template v-for="key in object._schemaMix">
      <dashboard-editor-object v-if="key.name&&key.type!=='Array'&&$yin.structureType.includes(key.type)"
                               :parent="object" :_key="key"/>
      <key-input v-else-if="key.name"
                 v-model="object[key.name]" :_key="key" :key="key.name" :object="object"/>
    </template>


    <!--    <n-popconfirm @positive-click="deleteSelf" >-->
    <!--      <template #trigger>-->
    <!--        <n-button secondary :loading="deleting" block type="warning">删除</n-button>-->
    <!--      </template>-->
    <!--      确定删除？-->
    <!--    </n-popconfirm>-->

  </div>
</template>

<style scoped lang="less">
.dashboardEditor {
  .headerBox {
    padding-bottom: 13px;
    border-bottom: var(--color-border) 1px solid;
    margin-bottom: 13px;


    .titleBox {
      display: flex;
      justify-content: space-between;
      align-items: center;
    }
  }
}
</style>
