<script lang="ts">
import {defineComponent} from 'vue'

export default defineComponent({
  name: "dashboardEditorObject",
  inject: ['screen', 'objectList', 'keyList'],
  props: ['_key', 'parent'],
  data() {
    return {
      object: {},
      models: []
    }
  },
  computed: {
    title() {
      if (this._key && this.parent) {
        if (!this.object._id)
          return this._key.title + ': ' + (this.object._title || '加载中')
        else if (this._key.title === this.object._title)
          return this.object._title
        else
          return this._key.title + ': ' + this.object._title
      } else
        return ''
    }
  },
  methods: {
    async init() {
      if (this.parent) {
        try {
          this.object = await this.parent[this._key.name]
        } catch (e) {
          this.object._title = '未创建'
          this.models = await this.parent[this._key.name].models()
        }
      }
    },
    menuSel() {
      if (this.object._id) {
        this.screen.target.push(this.object._place.valueOf())
      }
    },
    async create(model) {
      this.object = await this.parent[this._key.name].create({_model: model._id})
    }
  },
  mounted() {
    this.init()
  },
})
</script>

<template>
  <n-card :title="title" size="small" class="dashboardEditorObject" :style="{cursor:object._place?'pointer':''}"
          @click="menuSel">
    {{ object._place }}
    <template #action v-if="!object._place">
      <n-input-group>
        <n-input-group-label>创建：</n-input-group-label>
        <n-button v-for="model in models" v-if="models.length" @click="create(model)">
          {{ model._title }}
        </n-button>
        <n-input-group-label v-else>
          无可创建的模型
        </n-input-group-label>
      </n-input-group>
    </template>
  </n-card>
</template>

<style scoped lang="less">
.dashboardEditorObject {
  cursor: default;
  margin: 13px 0;


}
</style>
