<template>
  <div class="dashboard" @click="menuHide=true">
    <header>
      <div class="mainBox">
        <n-button class="menu" @click.stop="menuHide=!menuHide" v-if="showMenu">
          <n-icon>
            <menu-filled/>
          </n-icon>
        </n-button>
        <h3>{{ dashboardObject._title }}</h3>
      </div>
      <div class="metaBox">
        <!--        <div class="userBox">-->
        <!--          <p>{{ $yin.me._title || '我' }}</p>-->
        <!--        </div>-->
        <n-button-group>
          <n-popconfirm @positive-click="deleteSelf" v-if="parent">
            <template #trigger>
              <n-button :loading="deleting">删除</n-button>
            </template>
            是否删除{{ dashboardObject._title || dashboardObject._place }}？
          </n-popconfirm>
          <n-button>{{ $yin.me._title }}</n-button>
        </n-button-group>
      </div>
    </header>
    <div class="mainBody">
      <div class="sideBar" v-if="showMenu" :class="{menuHide}" @click.stop>
        <dashboard-menu-item @menuSel="menuSel" :root="dashboardObject" v-if="dashboardObject._id"/>
      </div>
      <dashboard-editor class="mainBox" :class="{showMenu}" :object="objectEditing" v-if="objectEditing._id"/>
      <!--      <div class="nodeInfo">-->
      <!--        screen-->
      <!--        <pre>{{ screen }}</pre>-->
      <!--        objectList-->
      <!--        <pre>{{ objectList }}</pre>-->
      <!--        keyList-->
      <!--        <pre>{{ keyList }}</pre>-->
      <!--      </div>-->
    </div>
  </div>
</template>

<script>
import {computed, nextTick} from "vue";
import {MenuFilled} from '@vicons/material'
import {yin} from 'yin-core-client'
import YinMenuItem from "@/components/node/menu/yinMenuItem.vue";
import DashboardMenuItem from "@/components/dashboard/dashboardMenuItem.vue";
import DashboardEditor from "@/components/dashboard/dashboardEditor.vue";


export default {
  name: "Dashboard",
  components: {
    MenuFilled,
    DashboardEditor, DashboardMenuItem, YinMenuItem
  },
  props: ['object', 'target'],
  provide() {
    return {
      objectList: computed(() => this.objectList),
      keyList: computed(() => this.keyList),
      screen: computed(() => this.screen),
    }
  },
  data() {
    return {
      screen: {
        target: []
      },
      objectList: {},
      keyList: {},
      dashboardObject: {},
      objectEditing: {},
      parent: null,
      deleting: false,
      showMenu: false,
      menuHide: true
    }
  },
  methods: {
    async init(e) {
      this.parent = this.$route.query.parent
      if (this.target)
        this.dashboardObject = await yin.get(this.target[0])
      else
        this.dashboardObject = this.object
      document.title = this.dashboardObject._title || this.dashboardObject._place


      this.objectEditing = this.dashboardObject
      this.screen.target = this.target


      for (let s of this.dashboardObject._schemaMix) {
        if (s.type !== 'Array' && yin.structureType.includes(s.type)) {
          this.showMenu = true
          return
        }
      }
    },
    menuSel(e = []) {
      this.screen.target = e.reverse()
    },
    async targetChange(target) {
      this.objectEditing = {}
      await nextTick()
      this.objectEditing = await yin.get(target[target.length - 1])
      this.$router.push({name: 'dashboard', params: {target: target}, query: {parent: this.parent}})
    },
    async deleteSelf() {
      this.deleting = true
      await this.dashboardObject._delete()
      if (this.$wx)
        history.back()
      else
        window.close()
    }
  },
  watch: {
    'target'(t, _t) {
      if (_t && t[0] !== _t[0])
        this.init()
    },
    'screen.target': {
      handler: 'targetChange',
      deep: true
    }
  },
  mounted() {
    if (!this.$yin.me._id)
      this.$router.push('/auth')
    else
      this.init()
  }
}
</script>

<style scoped lang="less">
.dashboard {
  padding-top: 65px;

  header {
    position: fixed;
    top: 0;
    width: 100vw;
    z-index: 1000;
    height: 64px;
    border-bottom: var(--color-border) 1px solid;
    background-color: var(--color-background);
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 21px;

    .mainBox {
      display: flex;
      align-items: center;


    }
  }

  .mainBody {
    min-height: calc(100vh - 65px);


    .sideBar {
      position: fixed;
      z-index: 1000;
      background-color: var(--color-background);
      min-width: 143px;
      height: calc(100vh - 65px);
      border-right: var(--color-border) 1px solid;
      overflow-y: auto;
      padding: 4px;

      &::-webkit-scrollbar {
        display: none;
      }

    }

    .mainBox {
      padding: 21px;
      overflow: auto;

      box-sizing: border-box;

      .editor {
        margin: 0 auto;
        min-width: 495px;


        .fnBox {
          display: flex;
          justify-content: space-between;
        }
      }
    }
  }

  .nodeInfo {
    position: fixed;
    z-index: 0;
    top: 0;
    right: 0;
    user-select: none;
    opacity: 0.4;
  }
}

@media (min-width: 901px) {

  .dashboard {
    --menu-width: calc(9vw + 143px);

    header {
      .mainBox {
        .menu {
          display: none;
        }
      }
    }

    .mainBody {
      .sideBar {
        width: var(--menu-width);
      }

      .showMenu {
        margin-left: var(--menu-width);
      }
    }


  }
}

@media (max-width: 900px) {
  .dashboard {
    header {
      .mainBox {
        .menu {
          display: unset;
          margin-right: 13px;
        }
      }
    }

    .mainBody {
      .sideBar {
        width: 232px;

        &.menuHide {
          display: none;
        }
      }

      .mainBox {
        margin-left: 0;
      }
    }
  }
}

</style>
