<script lang="ts">
import {defineComponent} from 'vue'

export default defineComponent({
  name: "dashboardMenuItem",
  inject: ['screen', 'objectList', 'keyList'],
  props: ['_key', 'parent', 'root'],
  emits: ['menuSel'],
  data() {
    return {
      object: {},
      // open: false,
      // target: false
    }
  },
  computed: {
    title() {
      if (this.root)
        return '主页'
      else {
        if (!this.object._id)
          return this._key.title + ': ' + (this.object._title || '加载中')
        else if (this._key.title === this.object._title)
          return this.object._title
        else
          return this._key.title + ': ' + this.object._title
      }
    },
    main() {
      if (this.root || !this.object._id)
        return true
      return this.objectList[this.place] === this.parent?._place.valueOf()
    },
    place() {
      return this.object._place?.valueOf() || this.object.api?.place.valueOf()
    },
    open() {
      if (this.root)
        return false
      const oi = this.screen.target.indexOf(this.place)
      if (oi !== -1)
        return this.screen.target[oi - 1] === this.parent?._place.valueOf()
    },
    target() {
      if (this.main)
        return this.screen.target[this.screen.target.length - 1] === this.place
    }
  },
  methods: {
    async init() {
      if (this.root) {
        this.object = this.root
        this.objectList[this.place] ??= 'dashboard'
      } else {
        try {
          this.object = await this.parent[this._key.name]
          this.objectList[this.place] ??= this.parent._place.valueOf()
        } catch (e) {
          this.object._title = '未创建'
        }
      }
    },
    menuSel(e?) {
      if (this.place)
        if (e) {
          e.push(this.place)
          this.$emit('menuSel', e)
        } else if (this.main) {
          this.$emit('menuSel', [this.place])
        } else
          this.screen.target = [this.place]
    },
  },
  watch: {
    main() {
      this.objectList[this.place] ??= this.parent?._place.valueOf()
    }
  },
  mounted() {
    this.init()
    if (this.parent)
      this.parent._on('update', this.init)
  },
  beforeUnmount() {
    if (this.main)
      delete this.objectList[this.place]
    if (this.parent)
      this.parent._removeEvent('update', this.init)
  }
})
</script>

<template>
  <div class="dashboardMenuItem" :class="{open,target}" @click="menuSel()">
    <div class="itemMain">
      <div class="main">
        <p>{{ title }}<span v-if="!main">*</span></p>
      </div>
      <p>{{ object.meta }}</p>
    </div>
    <p>{{ object.info }}</p>
  </div>
  <template v-if="root" v-for="key in root._schemaMix">
    <dashboard-menu-item v-if="key.type!=='Array'&&$yin.structureType.includes(key.type)"
                         @menuSel="menuSel"
                         :_key="key"
                         :parent="root"/>
  </template>
  <div v-else-if="open" style="margin-left: 13px">
    <template v-for="key in object._schemaMix">
      <dashboard-menu-item v-if="key.type!=='Array'&&$yin.structureType.includes(key.type)"
                           @menuSel="menuSel"
                           :_key="key"
                           :parent="object"/>
    </template>
  </div>
</template>

<style scoped lang="less">
.dashboardMenuItem {
  user-select: none;
  cursor: pointer;
  padding: 13px;
  border-radius: var(--border-radius-inner);
  margin: 4px;

  .itemMain {
    position: relative;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;


    .main {
      display: flex;
      align-items: center;
      margin-right: 8px;

      .fnIcon {
        margin-right: 8px;
      }
    }

    & > p {
      font-size: smaller;
      opacity: .8;
    }


    .children {
      display: none;
      left: calc(100% + 3px);
      top: 0;
    }
  }

  & > p {
    transition: all 0.2s 0.2s;
    font-size: smaller;
    margin-left: 29px;
    line-height: 0;
    opacity: 0;

  }

  &:hover {
    background-color: var(--color-background-mute);

    .itemMain {
      .children {
        display: unset;
      }
    }

    & > p {
      line-height: unset;
      opacity: 0.8;
    }
  }

  &.open {
    color: var(--color-text-active);
  }

  &.target {
    color: var(--color-text-active);
    background-color: var(--color-background-active);
  }
}
</style>

